@charset "utf-8";

body{
 color: #464c54;
 font-size: 18px;
 line-height: 1.6;
 font-family: 'Varela Round', sans-serif;
}
ul > li {
 list-style-type: disc;
}
h1, h2, h3, h4, h5, h6 {
 -webkit-font-smoothing: antialiased;
 color: black;
 font-weight: 700;
 font-family: 'Montserrat', sans-serif;
}
strong {
 font-weight: bold;
}
.icon-block {
 padding: 0 15px;
}
.icon-block .material-icons {
	font-size: inherit;
}
.shadow-demo {
 background-color: #26a69a;
 width: 100px;
 height: 100px;
 margin: 20px auto;
}
h1.page-title {
 font-weight: 500;
 font-size: 2.3em;
 text-align: center;
 color: #29b6f6;
}
.col.grid-example {
 border: 1px solid #eee;
 margin: 7px 0;
 text-align: center;
 line-height: 50px;
 font-size: 28px;
 background-color: tomato;
 color: white;
 padding: 0;
}
.collection .collection-item.avatar {
 padding-right: 30px;
}
.last-post {
 padding: 10px 0 15px;
}
.last-post .collection .collection-item.avatar>.date-post {
 position: absolute;
 width: 54px;
 height: 54px;
 overflow: hidden;
 left: 10px;
 display: inline-block;
 padding: 16px 0;
 vertical-align: middle;
 background: #ad1457;
 color: #fff;
 text-align: center;
}
.txt-center {
 text-align: center;
}
.post-header h1.post-title {
 color: #29b6f6;
 font-size: 2.8em;
 font-weight: 800;
}
.post-header time {
 color: #cacaca;
 font-size: 0.9em;
}
p.rss-subscribe {
 margin: 0;
}
figcaption {
 display: block;
 background: #6195FF30;
 padding: 1em;
 min-height: 50px;
 box-sizing: border-box;
}
figure {
 margin: 0;
 &.responsive {
 max-width: 50%;
 max-height: auto;
 }

 p.step {
 margin-top: 0px;
 color: #868f9b;
 }
}

hr {
 margin: 40px 0;
}
.sidenav {
 width: 238px;
 position: fixed;
 top: 64px;
 bottom: 0px;
 overflow-x: hidden;
 padding-top: 20px;
 background-color: #e9e9e9;
}

.bgwrap {
 background: #e9e9e9;
 display: flex;
}
.sidenav a {
 padding: 6px 8px 6px 16px;
 text-decoration: none;
 display: block;
}
.sidenav a:hover {
 color: #65aecf;
 }
.main {
 margin-left: 0px;
 padding: 0px 0px;
 background: white;
 margin-left: 238px;
 width: calc(100% - 238px);
}
@media only screen and (max-width: 993px) {
 .sidenav {
 display: none;
 }
 .main {
 margin-left: 0px;
 width: 100%;
 }
}
#partners {
 max-width: 80%;
 margin: auto;
 display: block;
}
.partners:after{
 visibility: hidden;
 display: block;
 font-size: 0;
 content: " ";
 clear: both;
 height: 0;
}
.partners {
 margin: auto;
 padding-left: 40px;
 padding-right: 40px;
 position: relative;
 text-align: center;
 border-radius: 5px;
 width: 100%;
 height: auto;
 display: inline-block;
 padding-top: 10px;
 padding-bottom: 10px;
 vertical-align: middle;
}
.partner-img {
 width: 100%;
 max-width: 300px;
 margin: auto;
 padding-top: 10px;
 padding-bottom: 10px;
 padding-left: 20px;
 padding-right: 20px;
 text-align: center;
 vertical-align: middle;
}
@media only screen and (max-width: 993px) {
 .partner-img {
 padding: 3px 5px;
 }
 .partners {
 padding: 0px;
 }
 .row {
 .col {
 padding: 0px;
 max-width: 100%;
 }
 }
}

.fancybox-caption {
 font-size: 18px;
}

:not(pre) > code, :not(pre) > kbd, :not(pre) > samp {
 font-family: 'Roboto Mono',monospace;
 color: #f0506e;
 white-space: nowrap;
 font-size: 0.8em;
}

pre > code {
 font-family: 'Roboto Mono',monospace;
 color: #f0506e;
 font-size: 0.8em;
}


@media (max-width: 400px) {
 .brand-logo img {
 display: none;
 }

 nav {
 height: 40px;
 line-height: 40px;

 .button-collapse {
 height: 40px;
 margin: 0 10px;
 }

 i {
 height: 40px !important;
 line-height: 40px !important;
 }
 }
}

.alert {
 padding: 20px;
 background-color: #f44336;
 color: white;
 opacity: 0.83;
 transition: opacity 0.6s;
 margin-bottom: 15px;
}

.side-nav li > a {
 overflow-x: hidden;
 text-overflow: ellipsis;
 white-space: nowrap;
}

.side-nav {
 padding-left: 0px;
}

#galerie {
 max-width: 80%;
 margin: auto;
 display: block;
}

img.crisp {
 image-rendering: pixelated; /* Chrome */
 image-rendering: crisp-edges; /* Firefox */
}
